body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
nav {
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Yeti {
    width: 80px;
}
.preloader {
  text-align: center;
}

.error_message {
  color: red;
}

input {
  width: 90%;
  padding: 1em;
  box-sizing: border-box;
  display: block;
  margin: 1em auto;
  border: 2px solid rgba(0, 0, 0, 0);
  font-size: 14px;
}

button {
    font-size: 14px;
    padding: 0.9em 1.3em;
    cursor: pointer;
    border: none;
    border-radius: 6px;
    transition: .35s ease;
    background-color: aquamarine;
    margin: 1em 0.5em;
}

button:hover {
  background-color: #ffb6b6;
}

button:focus, input:focus, textarea:focus {
  outline: none;
}

input:focus {
  border-bottom: 2px solid aquamarine;
}


p {
    -webkit-margin-before: 0em;
            margin-block-start: 0em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
}

.cards {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.Post {
    width: 90%;
}

.Post_user {
    border-top: 1px solid #d3d3d3;
    background-color: white;
    text-align: right;
    padding: 1.5em;
    color: gray;
    font-size: 12px;
}

.Post_title {
    font-weight: bold;
    font-size: 18px;
}

.Post_image {

}

.Post_info {
    background-color: white;
    margin-bottom: 2em;
    box-sizing: border-box;
    padding: 1em;
}

img {
    max-width: 100%;
}

.btn_create, .btn_submit {
    width: 90%;
    display: block;
    margin: 2em auto;
}
body {
  margin: 0;
  font-size: 14px;
  background-color: #f3f3f3;
}

.App_centered {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
}
